import { Component, OnInit } from '@angular/core';
import { AdminServiceService } from 'src/app/service/admin-service.service';
import { Company } from 'src/app/models/company';

@Component({
  selector: 'app-add-company',
  templateUrl: './add-company.component.html',
  styleUrls: ['./add-company.component.css']
})
export class AddCompanyComponent implements OnInit {

  constructor(private myService: AdminServiceService) { }
name:string;
email:string;
password:string;


  ngOnInit() {
  }

  public addCompany(){
    this.myService.addCompany(new Company(0,this.name,this.email,this.password)).subscribe(
      Company=>{
        alert("company "+ this.name+" has been added successfully !")
      }, err => {
        alert("oops! "+ err.error)
      }
    )
  }

  
  
}
