import { Component, OnInit } from '@angular/core';
import { Company } from 'src/app/models/company';
import { AdminServiceService } from 'src/app/service/admin-service.service';



@Component({
  selector: 'app-update-company',
  templateUrl: './update-company.component.html',
  styleUrls: ['./update-company.component.css']
})
export class UpdateCompanyComponent implements OnInit {

  constructor(private myService: AdminServiceService) { }
  id:number
  name: string;
  email:string;
  password:string;
  ngOnInit() {
  }
  public updateCompany(){
    this.myService.updateCompany(new Company(this.id,this.name,this.email,this.password)).subscribe(
      Company=>{
        alert("customer "+ this.name+" updated successfully !")
      },err=>{
        alert("oops! "+ err.error)
      }
      )
    }
}
