import { Component, OnInit } from '@angular/core';
import { AdminServiceService } from 'src/app/service/admin-service.service';
import { Company } from 'src/app/models/company';

@Component({
  selector: 'app-get-all-companies',
  templateUrl: './get-all-companies.component.html',
  styleUrls: ['./get-all-companies.component.css']
})
export class GetAllCompaniesComponent implements OnInit {

  constructor(private myService: AdminServiceService) { }
  companies:Company[]
  ngOnInit() {
  this.myService.getAllCompanies().subscribe(
    companies =>{
      this.companies = companies
    }, err=>{
      alert("oops something went wrong...")
      console.log(err);
    }
  )
  
}
public deleteCompany(id:number){
  this.myService.deleteCompany(id).subscribe(
    comp=>{
      alert("company deleted succssefully !")
      for(let i=0; i< this.companies.length; i++){
        if(this.companies[i].id == id){
          this.companies.splice(i, 1);
          break;
        }
      }
    }, err => {
      alert("oops! "+ err.error)
    }
  )
}

}
