import { Component, OnInit } from '@angular/core';
import { AdminServiceService } from 'src/app/service/admin-service.service';
import { Customer } from 'src/app/models/customer';

@Component({
  selector: 'app-add-customer',
  templateUrl: './add-customer.component.html',
  styleUrls: ['./add-customer.component.css']
})
export class AddCustomerComponent implements OnInit {

  constructor(private myService: AdminServiceService) { }
  firstName:string;
  lastName: string;
  email:string;
  password:string;
  ngOnInit() {
  }
  public addCustomer(){
    
    this.myService.addCustomer(new Customer(0,this.firstName,this.lastName,this.email,this.password)).subscribe(
      Customer=>{
      alert("customer "+ this.firstName+" "+this.lastName+" added successfully !")
      }, err => {
        alert("oops! "+ err.error)
      }
    )
  }
}
