import { Component, OnInit } from '@angular/core';
import { Customer } from 'src/app/models/customer';
import { AdminServiceService } from 'src/app/service/admin-service.service';

@Component({
  selector: 'app-update-customer',
  templateUrl: './update-customer.component.html',
  styleUrls: ['./update-customer.component.css']
})
export class UpdateCustomerComponent implements OnInit {

  constructor(private myService: AdminServiceService) { }
  id:number;
  firstName:string;
  lastName: string;
  email:string;
  password:string;
  ngOnInit() {
  }
  public updateCustomer(){
    this.myService.updateCustomer(new Customer(this.id,this.firstName,this.lastName,this.email,this.password)).subscribe(
      Customer=>{
        alert("customer "+ this.firstName+" "+this.lastName+" added successfully !")
      },err=>{
        alert("oops! "+ err.error)
      }
    )
  }
}
