import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Company } from '../models/company';
import {HttpClient} from '@angular/common/http';
import { Customer } from '../models/customer';
@Injectable({
  providedIn: 'root'
})
export class AdminServiceService {

  constructor(private httpClient: HttpClient) { }

public getAllCompanies(): Observable<Company[]>{
  return this.httpClient.get<Company[]>("/Admin/getAllCompanies/"+localStorage.myToken)
}
public addCompany(company:Company){
  return this.httpClient.post<Company>("/Admin/addCompany/"+ localStorage.myToken,company);
}
public updateCompany(company:Company){
  return this.httpClient.put<Company>("/Admin/updateCompany/" + localStorage.myToken, company);
}
public deleteCompany(id:number){
  return this.httpClient.delete("/Admin/deleteCompany/"+localStorage.myToken+"/" + id, {responseType:'text'});
}
public getOneCompany(id:number){
  return this.httpClient.get<Company>("/Admin/getOneCompany/" + localStorage.myToken + "/"+ id);
}


public getAllCustomers(): Observable<Customer[]>{
  return this.httpClient.get<Customer[]>("/Admin/getAllCustomers/"+localStorage.myToken)
}
public addCustomer(customer:Customer){
  return this.httpClient.post<Customer>("/Admin/addCustomer/"+ localStorage.myToken, customer);
}
public updateCustomer(customer:Customer){
  return this.httpClient.put<Customer>("/Admin/updateCustomer/"+ localStorage.myToken, customer);
}
public deleteCustomer(id:number){
  return this.httpClient.delete("/Admin/deleteCustomer/"+localStorage.myToken+"/" + id, {responseType:'text'});
}
public getOneCustomer(id:number){
  return this.httpClient.get<Customer>("/Admin/getOneCustomer/"+ localStorage.myToken + "/"+ id);
}


}
